import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';
import * as _ from 'lodash';

import { Difficulty } from '../models/difficulty';
import { ScoreRecord, ScoreRecordGroup } from '../models/score-record';

@Injectable()
export class HighscoresService {

  constructor(private storage: Storage) { }

  /**
   * Returns score records
   * @returns {Promise<ScoreRecordGroup[]>}
   */
  async getAllScoresGrouped(): Promise<ScoreRecordGroup[]> {
    const allKeys = await this.storage.keys();
    const keys = allKeys.filter(k => k.startsWith('scoring.'));
    let data: ScoreRecordGroup[] = [];
    for (let k of keys) {
      const records = await this.storage.get(k) as ScoreRecord[];
      data.push({
        levelIdent: records[0].levelIdent,
        difficulty: records[0].difficulty,
        records: _.chain(records).sortBy('score').reverse().take(5).value(),
      });
    }
    return data;
  }

  /**
   * Returns highscore for level and difficulty, 0 if there are no recorded scores
   * @param {string} levelIdent
   * @param {Difficulty} difficulty
   * @returns {Promise<number>}
   */
  async getHighscore(levelIdent: string, difficulty: Difficulty): Promise<number> {
    const result = await this.storage.get(this.getStorageKey(levelIdent, difficulty)) as ScoreRecord[];
    if (result && result.length) {
      return _.sortBy(result, 'score').reverse()[0].score;
    }
    return 0;
  }

  /**
   * Adds the score to the database, returns promise containing boolean of whether this is a highscore
   * @param {string} levelIdent
   * @param difficulty
   * @returns {Promise<boolean>} true is highscore
   */
  async addScore(levelIdent: string, difficulty: Difficulty, score: number): Promise<boolean> {
    let existingScores: ScoreRecord[] = await this.storage.get(this.getStorageKey(levelIdent, difficulty)) as ScoreRecord[];
    if (!existingScores) {
      existingScores = [];
    }
    const newRecord: ScoreRecord = {
      levelIdent,
      difficulty,
      score,
      date: new Date(),
    };
    const isHighscore = existingScores.length && existingScores.every(otherRecord => otherRecord.score < score);
    existingScores.push(newRecord);
    await this.storage.set(this.getStorageKey(levelIdent, difficulty), existingScores);
    return isHighscore;
  }

  private getStorageKey(levelIdent: string, difficulty: Difficulty) {
    return `scoring.${levelIdent}.${difficulty}`;
  }

}



