import { Component } from '@angular/core';
import { ActionSheetController, NavController, NavParams } from 'ionic-angular';
import * as _ from 'lodash';

import { DataService } from '../../services/data.service';
import { Level } from '../../models/level';
import { Difficulty } from '../../models/difficulty';
import {GamePage} from '../game/game';

class LevelGroup {
  name: string;
  items: Level[];
}

@Component({
  selector: 'page-level-select',
  templateUrl: 'level-select.html',
})
export class LevelSelectPage {
  data: LevelGroup[] = [];
  level: Level;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private dataService: DataService,
    private actionSheetCtrl: ActionSheetController,
    ) { }

  ionViewDidLoad() {
    const groups = _.groupBy(this.dataService.data.Levels, 'Category');
    this.data = Object.keys(groups).map(k => {
      return {
        name: k,
        items: groups[k],
      }
    });
  }

  levelSelected(level: Level) {
    if (!level.CanSelectDifficulty) {
      this.start(level, Difficulty.undefined);
      return;
    }

    this.actionSheetCtrl.create({
      title: level.Name,
      buttons: [
        {
          text: 'Easy',
          handler: () => this.start(level, Difficulty.easy),
        },
        {
          text: 'Medium',
          handler: () => this.start(level, Difficulty.medium),
        },
        {
          text: 'Hard',
          handler: () => this.start(level, Difficulty.hard),
        }
      ]
    }).present();
  }

  start(level: Level, difficulty: Difficulty) {
    this.navCtrl.push(GamePage, { level, difficulty });
  }

}



