import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, Injectable, Injector, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { Pro } from '@ionic/pro';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { HttpClientModule } from '@angular/common/http';
import { IonicStorageModule } from '@ionic/storage';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { LevelSelectPage } from '../pages/level-select/level-select';
import { DataService } from '../services/data.service';
import { GamePage } from '../pages/game/game';
import { GameService } from '../services/game.service';
import { GameCompletePage } from '../pages/game-complete/game-complete';
import { HighscoresService } from '../services/highscores.service';
import { HighscoresPage } from '../pages/highscores/highscores';
import { AboutPage } from '../pages/about/about';
import { InAppBrowser } from '@ionic-native/in-app-browser';

const IonicPro = Pro.init('d5b39d49', {
  appVersion: '0.0.1',
});

@Injectable()
export class MyErrorHandler implements ErrorHandler {
  ionicErrorHandler: IonicErrorHandler;

  constructor(injector: Injector) {
    try {
      this.ionicErrorHandler = injector.get(IonicErrorHandler);
    } catch(e) {
      // Unable to get the IonicErrorHandler provider, ensure
      // IonicErrorHandler has been added to the providers list below
    }
  }

  handleError(err: any): void {
    IonicPro.monitoring.handleNewError(err);
    // Remove this if you want to disable Ionic's auto exception handling
    // in development mode.
    this.ionicErrorHandler && this.ionicErrorHandler.handleError(err);
  }
}

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    LevelSelectPage,
    GamePage,
    GameCompletePage,
    HighscoresPage,
    AboutPage,
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    HttpClientModule,
    IonicStorageModule.forRoot(),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    LevelSelectPage,
    GamePage,
    GameCompletePage,
    HighscoresPage,
    AboutPage,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    IonicErrorHandler,
    [{ provide: ErrorHandler, useClass: MyErrorHandler }],
    DataService,
    GameService,
    HighscoresService,
    InAppBrowser,
  ]
})
export class AppModule {}



