import { Injectable } from '@angular/core';
import * as _ from 'lodash';
import { Subject } from 'rxjs/Subject';

import { Question } from '../models/question';
import { Difficulty } from '../models/difficulty';
import { Level } from '../models/level';
import { Location } from '../models/location';

@Injectable()
export class GameService {

  // Observables
  showLocation = new Subject<Location>();
  gameComplete = new Subject();

  // Constants
  readonly numQuestions = 10;
  readonly minAnswerScore = 100;
  readonly maxAnswerScore = 250;

  // Public state
  score = 0;
  correct = 0;
  questionNo = 0;
  currentQuestion: Question;
  answerFeedback: string;

  // Game config
  private level: Level;
  private difficulty: Difficulty;

  // Game state
  private locationPool: Location[];
  private locationNamesPool: string[];
  private questionAsked: Date;
  private paused: Date;
  private canAnswer: boolean = false;

  init(level: Level, difficulty: Difficulty) {
    this.level = level;
    this.difficulty = difficulty;
  }

  reset() {
    this.score = 0;
    this.correct = 0;
    this.questionNo = 0;
    this.currentQuestion = null;
    this.locationPool = _.shuffle(_.clone(this.level.Locations.filter(l => {
      return this.difficulty === Difficulty.undefined || l.Difficulty === this.difficulty;
    })));
    this.locationNamesPool = this.locationPool.map(l => l.Name);
    this.loadQuestion();
  }

  pause() {
    this.paused = new Date();
    this.canAnswer = false;
  }

  resume() {
    const elapsed = GameService.secondsBetween(this.paused, new Date());
    if (this.questionAsked) {
      this.questionAsked.setSeconds(this.questionAsked.getSeconds() + elapsed);
    }
    this.paused = null;
    this.canAnswer = true;
  }

  answerQuestion(index: number) {
    if (!this.canAnswer) return;
    this.canAnswer = false;

    const correct = this.currentQuestion.answer === index;
    if (correct) {
      const timeTaken = GameService.secondsBetween(this.questionAsked, new Date());
      const points =  timeTaken > 15.0
        ? this.minAnswerScore
        : Math.floor(this.minAnswerScore + ((this.maxAnswerScore - this.minAnswerScore) - (timeTaken / 15 * (this.maxAnswerScore - this.minAnswerScore))));
      this.score += points;
      this.correct++;
    }
    this.questionAsked = null;
    this.showFeedback(correct);

    setTimeout(() => {
      this.hideFeedback();
      this.loadQuestion();
    }, 500);
  }

  private loadQuestion() {
    this.questionNo++;
    if (this.questionNo > this.numQuestions) {
      this.gameComplete.next();
      return;
    }

    // Pick location
    const location = this.locationPool.shift();
    const answer = GameService.randomInt(0, 3);
    const answers = _.shuffle(this.locationNamesPool.filter(l => l !== location.Name))
      .slice(0, 4);
    answers[answer] = location.Name;
    this.currentQuestion = {
      answers,
      answer,
      lat: location.Lat,
      lng: location.Lng,
    };

    this.showLocation.next(location);

    this.canAnswer = true;
    this.questionAsked = new Date();
  }

  private showFeedback(correct: boolean) {
    this.answerFeedback = correct ? 'tick' : 'cross';
  }

  private hideFeedback() {
    this.answerFeedback = null;
  }

  static randomInt(min: number, max: number): number {
    return Math.floor(Math.random() * (max - min + 1) + min);
  }
  static secondsBetween(date1: Date, date2: Date) {
    return (date2.getTime() - date1.getTime()) / 1000;
  }

}



