import { Component } from '@angular/core';
import * as moment from 'moment';

import { HighscoresService } from '../../services/highscores.service';
import { ScoreRecord } from '../../models/score-record';
import { DataService } from '../../services/data.service';
import { Difficulty } from '../../models/difficulty';

class ScoreRecordGroupVM {
  title: string;
  records: ScoreRecord[];
}

@Component({
  selector: 'page-highscores',
  templateUrl: 'highscores.html',
})
export class HighscoresPage {

  data: ScoreRecordGroupVM[] = [];

  constructor(
    private dataService: DataService,
    private highscoresService: HighscoresService,
  ) {
  }

  ionViewDidLoad() {
    this.highscoresService.getAllScoresGrouped().then((groups) => {
      this.dataService.data.Levels.forEach((level) => {
        const matches = groups.filter(g => g.levelIdent === level.Identifier);
        this.data.push(...matches.map((m) => {
          const friendlyDifficulty = this.friendlyDifficulty(m.difficulty);
          return {
            title: level.Name + (friendlyDifficulty ? ': ' + friendlyDifficulty : ''),
            records: m.records,
          };
        }));
      });
    });
  }

  friendlyDifficulty(difficulty: Difficulty): string {
    switch (difficulty) {
      case Difficulty.easy: return 'Easy';
      case Difficulty.medium: return 'Medium';
      case Difficulty.hard: return 'Hard';
      default: return null;
    }
  }

  friendlyDate(date: Date): string {
    if (!date) return '';
    return moment(date).format('L LT');
  }
}



