import { Component } from '@angular/core';
import { ActionSheetController, NavController, NavParams } from 'ionic-angular';
import L from 'leaflet';

import { Difficulty } from '../../models/difficulty';
import { Level } from '../../models/level';
import { Location } from '../../models/location';
import { GameService } from '../../services/game.service';
import { Subject } from 'rxjs/Subject';
import { GameCompletePage } from '../game-complete/game-complete';
import { HighscoresService } from '../../services/highscores.service';

@Component({
  selector: 'page-game',
  templateUrl: 'game.html',
})
export class GamePage {
  readonly level: Level;
  readonly difficulty: Difficulty;
  viewReady = false;

  map: L.Map;
  private markers: any = {};
  private activeMarker: any;
  private iconActive: any;
  private iconInactive: any;

  private unsubscribe = new Subject<boolean>();

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public game: GameService,
    private actionSheet: ActionSheetController,
    private highscoresService: HighscoresService,
  ) {
    this.level = this.navParams.data.level;
    this.difficulty = this.navParams.data.difficulty;
  }

  ionViewDidLoad() {
    this.game.init(this.level, this.difficulty);
    this.game.showLocation.takeUntil(this.unsubscribe).subscribe((location) => {
      this.activateLocation(location);
    });
    this.game.gameComplete.takeUntil(this.unsubscribe).subscribe(() => {
      this.highscoresService.addScore(this.level.Identifier, this.difficulty, this.game.score).then((isHighscore) => {
        this.navCtrl.push(GameCompletePage, {
          game: this.game,
          isHighscore,
        }).catch();
      });
    });
  }

  ionViewWillEnter() {
    if (!this.viewReady) {
      this.viewReady = true;
      setTimeout(() => {
        this.initUI();
        this.game.reset();
      });
    }
  }

  ionViewWillUnload() {
    this.unsubscribe.next();
  }

  pause() {
    this.game.pause();
    this.actionSheet.create({
      title: 'Game Paused',
      buttons: [
        {
          text: 'Resume',
          handler: () => this.resume(),
        },
        {
          text: 'Restart',
          handler: () => this.game.reset(),
        },
        {
          text: 'Quit ',
          handler: () => this.navCtrl.popToRoot(),
        },
      ],
      enableBackdropDismiss: false,
    }).present().catch();
  }

  resume() {
    this.game.resume();
  }

  private activateLocation(location: Location) {
    const key = this.formatMarkerName(location.Lat, location.Lng);
    if (this.activeMarker) {
      this.activeMarker.setIcon(this.iconInactive);
    }
    this.activeMarker = this.markers[key];
    this.activeMarker.setIcon(this.iconActive);
    const { lat, lng } = this.activeMarker.getLatLng();
    this.map.setView([lat, lng], this.map.getZoom(), {
      animate: true,
      duration: 1,
    });
  }

  private convertScale(zoomScale: number): number {
    let converted = 6;
    switch (zoomScale) {
      case 0: converted = 4; break;
      case 1: converted = 4; break;
      case 2: converted = 4; break;
      case 3: converted = 5; break;
      case 4: converted = 6; break;
      case 5: converted = 7; break;
    }
    return converted;
  }

  private formatMarkerName(lat: number, lng: number): string {
    return lat.toString() + "," + lng.toString();
  }

  private initUI() {
    const zoom = this.convertScale(this.level.ZoomScale);
    this.map = L.map('map', {
      dragging: false,
      keyboard: false,
      scrollWheelZoom: false,
      tap: false,
      touchZoom: false,
      zoomControl: true,
      doubleClickZoom: 'center',
    }).setView([51.5074, 0.1278], zoom);
    L.tileLayer('https://stamen-tiles-{s}.a.ssl.fastly.net/terrain-background/{z}/{x}/{y}.{ext}', {
      attribution: 'Map tiles by <a href="http://stamen.com" target="_blank">Stamen Design</a>, <a href="http://creativecommons.org/licenses/by/3.0" target="_blank">CC BY 3.0</a> &mdash; Map data &copy; <a href="http://www.openstreetmap.org/copyright" target="_blank">OpenStreetMap</a>',
      minZoom: 2,
      maxZoom: 8,
      ext: 'png'
    }).addTo(this.map);

    this.iconActive = L.divIcon({ className: 'map-icon active' });
    this.iconInactive = L.divIcon({ className: 'map-icon inactive' });

    this.level.Locations.forEach((location) => {
      this.markers[this.formatMarkerName(location.Lat, location.Lng)] = L.marker([location.Lat, location.Lng], { icon: this.iconInactive }).addTo(this.map);
    });
  }
}



